:root {
  --container-width: 288px;
  --content-width: calc(100% - 15px);
  --grid-gutter: 24px;
  --grid-columns: 4;
  --alignwide-width: 100%;
  --contentwide-width: calc(100% - var(--grid-gutter));
}

@media (min-width: 36em) and (max-width: 47.99em) {
